import Audio from '../../components/luch-audio/luch-audio.vue'
export default {
	data() {
		return {
			array_yrn: {
				mode:'widthFix',
				image_yrn: 'https://www.dpm.org.cn/Uploads/Picture/2020/04/29/s5ea9744238b72.jpg',
				title_yrn: "故宫博物馆",
				p_yrn: "北京的代表性景点，古老中国的特征",
				ppp_yrn: "根据当前北京市疫情防控形势，故宫博物院自2020年5月1日起有序开放，实行预约、错峰、限流参观。相关事宜公告如下.有人说，不到西安，就不算到中国，不见兵马俑，就不算到西安。是的，西安的奇迹太多了，而兵马俑则可以说是奇迹中的奇迹。最古老的兵马俑，如今已经成了西安乃至中国的金字名片。",
				// explainTitle1_qj: "故宫博物馆讲解",
				guideName1_qj: "  三毛游",
				guideTag1_qj: "官方讲解"
			},
			current: {
			    poster: 'https://img-cdn-qiniu.dcloud.net.cn/uniapp/audio/music.jpg',
			    name: '博物馆讲解',
			    author: '云锦',
				src: 'https://img-cdn-qiniu.dcloud.net.cn/uniapp/audio/music.mp3'
			},
			picList_qj: {
				colName1: "一号坑",
				colName2: "车马俑",
				colName3: "立射俑",
				colName4: "跪射俑",
				exhName1: "展览一",
				exhName2: "展览二",
				exhName3: "展览三",
				exhName4: "展览四"
			},
			museumInfo: [],
			exhibitionList: [],
			collectionList: [],
		}
	},
	components: {
		Audio
	},
	methods: {
		goDetail_qj: function() {
			uni.navigateTo({
			    url: '../exhibition/exhibition'
			});
		},
		goDetail_yrn: function() {
			uni.navigateTo({
			    url: '../collection/collection'
			});
		},
		goFeed_yrn: function() {
			uni.navigateTo({
				url:'/pages/message/feedback/feedback'
			});
		},
		goExplain_yrn: function(){
			uni.navigateTo({
				url:'../explain/explain'
			});
		},
		goRecord_yrn:function(){
			uni.navigateTo({
				url:'../record/record'
			});
		},
		//获取博物馆信息
		async getMuseumInfo() {
			const res = await this.$myRequest({
				url: '/museumtest/75/'
			})
			// console.log(res)
			this.museumInfo = res.data.data.items
		},
		//获取展览信息
		async getExhibitionList() {
			const res = await this.$myRequest({
				url: '/exhibitiontest/75/'
			})
			console.log(res)
			this.exhibitionList = res.data.data.items
		},
		//获取藏品信息
		async getCollectionList() {
			const res = await this.$myRequest({
				url: '/collectiontest/75/'
			})
			// console.log(res)
			this.collectionList = res.data.data.items
		},
		//跳转到藏品页
		goCollectionInfo(pk) {
			uni.navigateTo({
			    url: '../collection/collection?pk='+pk
			})
			// console.log(pk)
		},
		//跳转到展览页
		goExhibitionInfo(pk) {
			uni.navigateTo({
				url: '../exhibition/exhibition?pk='+pk
			})
			console.log(pk)
		}
		
	},
	onLoad() {
		this.getMuseumInfo()
		this.getExhibitionList()
		this.getCollectionList()
	}
}
